<template>
  <form class="mhz-form">
    <slot></slot>
  </form>
</template>

<script>
export default {
  name: 'mForm',
  provide() {
    return {
      form: this
    }
  },
  props: {
    model: {//  
      type: Object 
    },
    rules: {//  
      type: Object
    }
  },
  components: {},
  data() {
    return {
      fields: []  //  
    };
  },
  watch: {},
  computed: {},
  created() {
    //   
    this.$on('on-form-item-add', field => {
      if (field) this.fields.push(field); 
    })
    this.$on('on-form-item-remove', field => {
      if (field) this.fields.splice(this.fields.indexOf(field), 1);
    })
  },
  mounted() {},
  methods: {
    resetFields() {
      this.fields.forEach(field => {
        field.resetField();
      })
    },
    //   promise
    validate(callback) {
      return new Promise((resolve) => {
        let valid = true;
        let count = 0;
        this.fields.forEach(field => {
          field.validate('', errors => {
            if (errors) {
              valid = false;
            }
            if (++count === this.fields.length) {
              //  
              resolve(valid);
              if (typeof callback === 'function') {
                callback(valid);
              }
            }
          })
        })
      })
    }
  },
};
</script>